export const toMMss = (time: number) => {
  const isOver = time < 0
  const min = Math.abs(parseInt(String(time / 60)))
  const sec = Math.abs(time % 60)
  return (
    (isOver ? '-' : '') + ('00' + min).slice(-2) + ':' + ('00' + sec).slice(-2)
  )
}

export const getProgressBarWidth = (ownTimeInSec: number, originOwnTimeInSec: number) => {

  const progress = (ownTimeInSec / originOwnTimeInSec) * 100
  if (progress <= 0) {
    return 0
  } else {
    return Math.max(1, progress)
  }

}
