import React from 'react';
import { ReactNode } from "react";
import { Button, ButtonProps } from '../Button/index';

// サポートするファイルのタイプ
const SUPPORT_IMG_FORMAT = "image/*";

export type UploadableImageButtonProps = {
  image?: ReactNode;
  onFileSelect: (filse: FileList) => void;
} & ButtonProps;

export const UploadableImageButton: React.FC<UploadableImageButtonProps> = ({
  label,
  image,
  onFileSelect,
  ...props
}) => {

  // Ref
  const hiddenRef = React.useRef<HTMLInputElement>(null);

  // Callback
  const handleClickButton = React.useCallback((event) => {
    event.stopPropagation();
    hiddenRef?.current?.click();
  }, []);

  const handleChangeFile = React.useCallback((event) => {
    event.stopPropagation();
    onFileSelect(event.target.files);
  }, [onFileSelect]);

  return (
    <>
      <Button
        childNode={(
          <>
            {image && (
              <>{image}</>
            )}
            {label && (
              <div
                dangerouslySetInnerHTML={{ __html: label.replaceAll('\n', '<br />') }}
              />
            )}
          </>
        )}
        onClick={handleClickButton}
        {...props}
      />
      <input
        hidden
        ref={hiddenRef}
        type="file"
        accept={SUPPORT_IMG_FORMAT}
        onChange={handleChangeFile}
      />
    </>
  );
};
