import React, { ReactNode } from 'react'
import { styled } from '@stitches/react'
import { UploadableImageButton } from '../../atoms/UploadableImageButton/index'
import { PanelerNameField } from '../../atoms/PanelerNameField/index'
import { PanelerTimeBarLabel } from '../../atoms/PanelerTimeBarLabel/index'
import { PanelerActiveButton } from '../PanelerActiveButton/index'
import { TimeChangeButton, TimeChangeType } from '../../atoms/TimeChangeButton/index'
import { Button } from '../../atoms/Button/index'

export type TimeChangeButtonInterface = {
  label: string;
  type: TimeChangeType;
  payload: { [k: string]: any };
};

export type PanelerPanelProps = {
  panelerImage?: ReactNode;
  panelerName: string;
  ownTimeInSec: number;
  originOwnTimeInSec: number;
  barHeight: number;
  barColor: string;
  progressColor: string;
  isTalkOn: boolean;
  timeChangeButtons: Array<TimeChangeButtonInterface>;
  onChangeProfileImage: (files: FileList) => void;
  onChangePanelerName: (name: string) => void;
  onChangeOwnTime: (ownTimeInSec: number, originOwnTimeInSec: number) => void;
  onClickTalkOnButton: () => void;
  onClickTalkOffButton: () => void;
  onClickTimeChangeButton: (seconds: number) => void;
  onClickTrashTimeChangeButton: (id: number) => void;
  onClickAddTimeChangeButton: () => void;
};
    
const _PanelerPanel: React.FC<PanelerPanelProps> = ({
  panelerImage,
  panelerName,
  ownTimeInSec,
  originOwnTimeInSec,
  barColor,
  barHeight,
  progressColor,
  isTalkOn,
  timeChangeButtons,
  onChangePanelerName,
  onChangeProfileImage,
  onChangeOwnTime,
  onClickTalkOnButton,
  onClickTalkOffButton,
  onClickTimeChangeButton,
  onClickTrashTimeChangeButton,
  onClickAddTimeChangeButton,
  ...props
}) => {

  const panelerTimeBarProps = {
    ownTimeInSec,
    originOwnTimeInSec,
    barColor,
    barHeight,
    progressColor,
    onChange: (text: string) => {
      const splited = text.split('/');
      if (splited.length === 2) {
        const newTimeInt = parseInt(splited[0]);
        const newOriginTimeInt = parseInt(splited[1]);
        if (!isNaN(newTimeInt) && !isNaN(newOriginTimeInt)) {
          onChangeOwnTime(newTimeInt, newOriginTimeInt);
        }
      }
    },
  };

  return (
    <div
      {...props}
    >
      <UploadableImageButton
        image={panelerImage ?? (
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512">
            <path d="M224 256c70.7 0 128-57.31 128-128s-57.3-128-128-128C153.3 0 96 57.31 96 128S153.3 256 224 256zM274.7 304H173.3C77.61 304 0 381.6 0 477.3c0 19.14 15.52 34.67 34.66 34.67h378.7C432.5 512 448 496.5 448 477.3C448 381.6 370.4 304 274.7 304z"/>
          </svg>
        )}
        onFileSelect={onChangeProfileImage}
        shape='square'
        border='none'
        padding='none'
        css={{
          width: 118,
          padding: 8,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
        }}
      />
      <div className="paneler-time">
        <PanelerNameField
          name={panelerName}
          onChange={onChangePanelerName}
          css={{
            height: 'calc(50% - 16px)',
            marginBottom: 16,
            '& > button': {
              fontSize: '1.5rem',
            },
          }}
        />
        <PanelerTimeBarLabel
          {...panelerTimeBarProps}
          css={{
            height: '50%',
          }}
        />
      </div>
      <PanelerActiveButton
        isOn={isTalkOn}
        label={isTalkOn ? '停止' : '開始'}
        onClick={isTalkOn ? onClickTalkOnButton : onClickTalkOffButton}
        border='none'
        css={{
          fontSize: '2.5rem',
          marginRight: 16,
          height: 200,
        }}
      />
      <div className="time-change-wrap">
        {timeChangeButtons.map((props: TimeChangeButtonInterface, i: number) => (
          <div
            key={`time-change-${i}`}
            className="time-change"
          >
            <TimeChangeButton
              i={i}
              onClickTime={onClickTimeChangeButton}
              onClickTrash={onClickTrashTimeChangeButton}
              shape='square'
              border='none'
              color='primary'
              css={{
                width: '100%',
                fontSize: '1.3rem',
              }}
              {...props}
            />
          </div>
        ))}
        <div
          className="time-change"
        >
          <Button
            label='+'
            shape='square'
            border='none'
            onClick={onClickAddTimeChangeButton}
            css={{
              width: '100%',
              fontSize: '2.5rem',
            }}
          />
        </div>
      </div>
    </div>
  );

};

export const PanelerPanel = styled(_PanelerPanel, {
  display: 'flex',
  '& .paneler-time': {
    margin: '40px 16px 0 16px',
  },
  '& .time-change-wrap': {
    display: 'flex',
    width: '100%',
    alignItems: 'center',
  },
  '& .time-change': {
    margin: 16,
    height: '60%',
    aspectRatio: 1,
  },
  '& > *': {
    marginTop: 40,
  },
});
