import React from 'react';
import { styled } from "@stitches/react";
import { toMMss } from '../../../Util';

export type EventTimeLabelProps = {
  name: string;
  time: number;
  onChangeName: (name: string) => void;
  onChangeTime: (time: number) => void;
}

const _EventTimeLabel: React.FC<EventTimeLabelProps> = ({
  name,
  time,
  onChangeName,
  onChangeTime,
  ...props
}) => {

  const onClickName = React.useCallback((event) => {

    const newName = prompt('イベント名を入力してください', name);
    if (newName !== null) {
      onChangeName(newName);
    }
    
  }, [name, onChangeName]);

  const onClickTime = React.useCallback((event) => {

    const newTime = prompt('イベント時間を秒で入力してください', String(time));
    if (newTime !== null) {
      const newTimeInt = parseInt(newTime);
      if (!isNaN(newTimeInt)) {
        onChangeTime(newTimeInt);
      }
    }

  }, [time, onChangeTime]);

  return (
    <div
      {...props}
    >
      <button
        onClick={onClickName}
      >
        {name}
      </button>
      <div>&nbsp;:&nbsp;</div>
      <button
        onClick={onClickTime}
      >
        {toMMss(time)}
      </button>
    </div>
  );

};

export const EventTimeLabel = styled(_EventTimeLabel, {
  display: 'flex',
  '& > button': {
    display: 'inline-block',
    cursor: 'pointer',
    border: 'none',
    padding: 8,
    minWidth: 280,
  },
  '& > div': {
    alignItems: 'center',
    display: 'flex',
    padding: '0 4px',
  },
});
