import React from 'react'
import { styled } from '@stitches/react'
import { getProgressBarWidth, toMMss } from '../../../Util'

export type PanelerTimeBarLabelProps = {
  ownTimeInSec: number;
  originOwnTimeInSec: number;
  barHeight: number;
  barColor: string;
  progressColor: string;
  onChange: (text: string) => void;
}

const _PanelerTimeBarLabel: React.FC<PanelerTimeBarLabelProps> = ({
  ownTimeInSec,
  originOwnTimeInSec,
  barColor,
  barHeight,
  progressColor,
  onChange,
  ...props
}) => {

  const onClickTime = React.useCallback((_) => {

    const newTime = prompt('現在の持ち時間と合計の持ち時間を秒で入力してください（スラッシュ区切り）', String(ownTimeInSec) + '/' + String(originOwnTimeInSec))
    if (newTime !== null) {
      onChange(newTime)
    }

  }, [ownTimeInSec, originOwnTimeInSec, onChange])

  return (
    <div
      {...props}
    >
      <div
        className="bar"
        style={{
          width: '100%',
          height: barHeight,
          backgroundColor: barColor,
        }}
      >
        <div
          className="own-bar bar"
          style={{
            width: `${getProgressBarWidth(ownTimeInSec, originOwnTimeInSec)}%`,
            height: barHeight,
            backgroundColor: progressColor,
          }}
        />
      </div>
      <button
        className="ownTime"
        onClick={onClickTime}
      >
        残り&nbsp;{toMMss(ownTimeInSec)}
      </button>
    </div>
  );

};

export const PanelerTimeBarLabel = styled(_PanelerTimeBarLabel, {
  width: 300,
  '& .bar': {
    borderRadius: 3,
  },
  '& .ownTime': {
    fontWeight: 'bold',
    fontSize: '2.5rem',
    border: 'none',
    background: 'none',
    cursor: 'pointer',
    height: 'calc(100% - 16px)',
  },
});
