import React, { useEffect, useRef, useState } from 'react'
import { EventProps, PlayerProps } from './Interfaces'
import { getProgressBarWidth, toMMss } from './Util'
import { Promise } from 'bluebird'

const drawCurveText = (img: ImageBitmap) => {

  const canvas = document.createElement('canvas')
  const ctx = canvas.getContext('2d')!
  const subCanvas = document.createElement('canvas')
  const subCtx = subCanvas.getContext('2d')!
  canvas.width = img.width
  canvas.height = img.height
  subCanvas.width = 1

  for (let i = 0; i < canvas.width; ++i) {
    const height = canvas.height - 0.15 * i
    subCanvas.height = canvas.height
    subCtx.drawImage(img, -i, 0)
    ctx.drawImage(subCanvas, i, 0, 1, height)
  }
  return canvas.toDataURL()

}

const TEXT_COLOR = ['#E4007F', '#A40B5D', '#009944', '#FFE200']

function roundRect(
  ctx: CanvasRenderingContext2D,
  x: number,
  y: number,
  width: number,
  height: number,
  r = 20,
  fill = true,
  stroke = false,
) {
  let radius = {tl: r, tr: r, br: r, bl: r}
  ctx.beginPath()
  ctx.moveTo(x + radius.tl, y)
  ctx.lineTo(x + width - radius.tr, y)
  ctx.quadraticCurveTo(x + width, y, x + width, y + radius.tr)
  ctx.lineTo(x + width, y + height - radius.br)
  ctx.quadraticCurveTo(x + width, y + height, x + width - radius.br, y + height)
  ctx.lineTo(x + radius.bl, y + height)
  ctx.quadraticCurveTo(x, y + height, x, y + height - radius.bl)
  ctx.lineTo(x, y + radius.tl)
  ctx.quadraticCurveTo(x, y, x + radius.tl, y)
  ctx.closePath()
  if (fill) {
    ctx.fill()
  }
  if (stroke) {
    ctx.stroke()
  }
}

const Main: React.FC = () => {

  const playerNameUpdateFlagRef = useRef<boolean>(false)
  const [eventProps, setEventProps] = useState<null | EventProps>(null)
  const [playerProps, setPlayerProps] = useState<null | PlayerProps[]>(null)
  const [playerNameSrcList, setPlayerNameSrcList] = useState<(null | string)[]>([null, null, null, null])

  useEffect(() => {

    if (playerProps === null || !playerNameUpdateFlagRef.current) return
    Promise.map(playerProps, (playerProp, i) => {

      const name = playerProp.panelerName
      const scale = window.devicePixelRatio
      const width = 2048 / 3
      // const width = document.body.clientWidth / 3
      const canvas = document.createElement('canvas')
      document.body.appendChild(canvas)
      canvas.style.display = 'none'

      const ctx = canvas.getContext('2d')!
      canvas.style.letterSpacing = '-5px'
      ctx.clearRect(0, 0, canvas.width, canvas.height)
      ctx.scale(scale, scale)
      canvas.width = width

      ctx.font = 'bold 130px Palanquin'
      ctx.textAlign = 'start'
      ctx.textBaseline = 'top'

      const textSize = ctx.measureText(name)
      ctx.fillStyle = 'white'
      roundRect(ctx, 0, 0, textSize.width + 20, canvas.height - 15)

      ctx.strokeStyle = 'black'
      ctx.lineWidth = 10
      ctx.strokeText(name, 7, 15)

      ctx.fillStyle = TEXT_COLOR[i]
      ctx.fillText(name, 7, 15)

      return new Promise<string>(resolve => {
        const image = new Image()
        image.onload = async () => {
          resolve(drawCurveText(await createImageBitmap(image)))
        }
        image.src = canvas.toDataURL()
        canvas.parentNode?.removeChild(canvas)
      })

    }).then(x => {
      setPlayerNameSrcList(x)
    })

  }, [playerProps])

  useEffect(() => {

    document.body.className = 'main'

    const onMessage = (event: MessageEvent<any>) => {
      const data = event.data
      if (data.panelerPropsList) {
        const currentNames = playerProps?.map(x => x.panelerName) ?? null
        playerNameUpdateFlagRef.current = currentNames === null || (data.panelerPropsList as PlayerProps[]).map(x => x.panelerName).filter(x => currentNames.indexOf(x) === -1).length > 0
        setPlayerProps(data.panelerPropsList)
      }
      if (data.eventProps) setEventProps(data.eventProps)
    }
    window.addEventListener('message', onMessage, false)
    return () => {
      window.removeEventListener('message', onMessage)
    }

  }, [playerProps])

  return (
    <>
      <header>
        <div className="all-time">
          <span>{eventProps && toMMss(eventProps.eventTimeInSec)}</span>
        </div>
      </header>
      <main>
        {playerProps && playerProps.map((player, i) => (
          <div className={`player player0${i + 1}`} key={`player-${i}`}>
            <div className="player-score">
              <div className="score-bar">
                <div className="score-bar__elapsed"
                     style={{width: `${100 - getProgressBarWidth(player.ownTimeInSec, player.originOwnTimeInSec)}%`}}></div>
              </div>
            </div>
            <div className="player-name">
              {playerNameSrcList[i] && <img src={playerNameSrcList[i]!} key={`playerName-${i}`} alt=""/>}
            </div>
            <div className="player-icon">
              {player?.panelerImage && <img src={player.panelerImage} alt=""/>}
            </div>
            <div className="player-time">
              <span>{toMMss(player.ownTimeInSec)}</span>
            </div>
          </div>
        ))}
      </main>
    </>
  )
}

export default Main