import React from 'react';
import { styled } from "@stitches/react";

export type PanelerNameFieldProps = {
  name: string;
  onChange: (name: string) => void;
}

const _PanelerNameField: React.FC<PanelerNameFieldProps> = ({
  name,
  onChange,
  ...props
}) => {

  const onClickName = React.useCallback((event) => {

    const newName = prompt('パネラ名を入力してください', name);
    if (newName !== null) {
      onChange(newName);
    }
    
  }, [name, onChange]);

  return (
    <div
      {...props}
    >
      <button
        onClick={onClickName}
      >
        {name}
      </button>
    </div>
  );

};

export const PanelerNameField = styled(_PanelerNameField, {
  display: 'flex',
  '& > button': {
    display: 'inline-block',
    cursor: 'pointer',
    border: 'none',
    padding: 8,
    width: '100%',
  },
});
