import React from 'react';
import { ReactNode } from "react";
import { styled } from "@stitches/react";
import type * as Stitches from '@stitches/react';

export type ButtonColor = 'primary' | 'secondary';
export type ButtonShape = 'square';
export type ButtonBorder = 'none';
export type ButtonPadding = 'none';

export type ButtonProps = {
  label?: string;
  childNode?: ReactNode;
  color?: ButtonColor;
  shape?: ButtonShape;
  border?: ButtonBorder;
  padding?: ButtonPadding;
  onClick?: (event: any) => void;
  css?: Stitches.CSS;
};

const replaceLf = (label?: string) => {
  return label?.replaceAll('\\n', '<br />')?.replaceAll('\n', '<br />') ?? '';
}

const _Button: React.FC<ButtonProps> = ({
  label,
  childNode,
  ...props
}) => {

  return (
    <button
      type="button"
      {...props}
    >
      {label && (
        <span
          dangerouslySetInnerHTML={{ __html: replaceLf(label) }}
        />
      )}
      {childNode && (
        <>
          {childNode}
        </>
      )}
    </button>
  );

};

export const Button = styled(_Button, {
  cursor: 'pointer',
  borderRadius: 8,
  color: 'black',
  backgroundColor: '#EFEFEF',
  variants: {
    color: {
      primary: {
        color: 'white',
        backgroundColor: 'blue',
      },
      secondary: {
        color: 'white',
        backgroundColor: 'red',
      },
    },
    shape: {
      square: {
        aspectRatio: 1,
      },
    },
    border: {
      none: {
        border: 'none',
      }
    },
    padding: {
      none: {
        padding: 0,
      }
    },
  },
});
