import React from 'react'
import { styled } from '@stitches/react'
import { Button } from '../../atoms/Button/index'
import { EventTimeLabel } from '../../atoms/EventTimeLabel/index'
import { PanelerPanel, PanelerPanelProps } from '../../molecules/PanelerPanel/index'
import { toMMss } from '../../../Util'

export type TopPageProps = {
  isStarted: boolean;
  eventName: string;
  eventTimeInSec: number;
  panelerPanelProps: Array<PanelerPanelProps>;
  onClickStartButton: (event: any) => void;
  onClickOpenButton: (event: any) => void;
  onChangeEventName: (name: string) => void;
  onChangeEventTime: (time: number) => void;
};

const _TopPage: React.FC<TopPageProps> = ({
                                            isStarted,
                                            eventName,
                                            eventTimeInSec,
                                            panelerPanelProps,
                                            onClickStartButton,
                                            onClickOpenButton,
                                            onChangeEventName,
                                            onChangeEventTime,
                                            ...props
                                          }) => {

  const eventTimeLabelProps = {
    name: eventName,
    time: eventTimeInSec,
    onChangeName: onChangeEventName,
    onChangeTime: onChangeEventTime,
  }

  return (
    <div
      {...props}
    >
      <header
        className="header"
      >
        <EventTimeLabel
          {...eventTimeLabelProps}
          css={{
            '& > button': {
              fontSize: '1.5rem',
            },
          }}
        />
        {!isStarted && (
          <Button
            onClick={onClickStartButton}
            label="開始"
            border="none"
            color="primary"
            css={{
              marginLeft: 16,
              width: 150,
              fontSize: '1.3rem',
            }}
          />
        )}
        <Button
          onClick={onClickOpenButton}
          label="Open"
          border="none"
          css={{
            marginLeft: 8,
            width: 130,
            fontSize: '1.3rem',
            backgroundColor: 'green',
            color: 'white',
          }}
        />
        <div className="time">
          イベント残り時間
          {toMMss(eventTimeInSec)}
        </div>
      </header>
      <div className="main-wrap">
        <div className="panel-wrap">
          {panelerPanelProps.map((panelerPanelProp: PanelerPanelProps, i: number) => (
            <PanelerPanel
              key={`paneler-panel-${i}`}
              {...panelerPanelProp}
            />
          ))}
        </div>
      </div>
    </div>
  );

};

export const TopPage = styled(_TopPage, {
  '& header': {
    display: 'flex',
    height: 100,
  },
  '& .main-wrap': {
    display: 'grid',
    height: 'calc(100% - 100px)',
  },
  '& .panel-wrap': {
    display: 'grid',
  },
  '& .time': {
    width: '-webkit-fill-available',
    height: 'calc(100% - 6px)',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    fontSize: '3.5rem',
    letterSpacing: 15,
    marginLeft: 16,
    fontWeight: 'bold',
    border: '3px solid red',
    backgroundColor: 'yellow',
    color: 'red',
    whiteSpace: 'nowrap',
  }
});
